// main page elements
body {
	font-size: 14px;
	// color: $dark
	font-family: 'Lato';
	font-weight: normal;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1225px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; }
	position: relative; }
.flex {
	display: flex; }
.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	color: white;
	border-radius: 4px;
	background: #F3AF5A; }
.main {
	padding-top: 171px;
	padding-bottom: 228px;
	background: linear-gradient(104.04deg, #35C9C5 14%, #00A9DB 84.88%);
	.one {
		position: absolute;
		right: 0;
		bottom: -155px; }
	.two {
		position: absolute;
		bottom: -165px;
		left: 0; }
	h1 {
		font-weight: bold;
		font-size: 50px;
		line-height: 54px;
		color: white;
		max-width: 554px; }
	p {
		margin-top: 42px;
		font-weight: 500;
		font-size: 22px;
		line-height: 22px;
		color: white;
		max-width: 468px; }
	.flex {
		position: relative;
		margin-top: 75px;
		gap: 24px;
		svg {
			position: absolute;
			left: 328px;
			margin-top: 10px; }
		a {
			max-width: 288px;
			height: 50px;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			font-weight: 800;
			font-size: 14px;
			line-height: 22px;
			&:hover {
				border: 2px solid #F3AF5A;
				background: inherit;
				color: #F3AF5A; }
			&:active {
				background: #E69837;
				color: white; }
			&:hover svg {
				fill: #F3AF5A; } }
		input {
			font-weight: 500;
			font-size: 14px;
			line-height: 22px;
			max-width: 520px;
			width: 100%;
			height: 50px;
			color: black;
			padding-left: 61px;
			border-radius: 4px; } } }
.three {
	margin-top: -40px;
	.flex {
		box-shadow: 0px 0px 6px rgba(102, 113, 115, 0.2);
		border-radius: 12px;
		align-items: center;
		justify-content: space-between;
		height: 207px;
		padding-left: 32px;
		padding-right: 32px;
		background: white;
		.line {
			background: #6D6D6D;
			width: 1px;
			height: 83px; }
		.three__item {
			align-items: center;
			display: flex;
			flex-direction: column;
			h4 {
				font-weight: bold;
				font-size: 18px;
				line-height: 22px;
				margin-bottom: 4px; }
			.number {
				color: #2C4267;
				font-weight: 300;
				font-size: 63px;
				line-height: 22px;
				margin-bottom: 31px; }
			p {
				font-size: 14px;
				line-height: 22px; }
			svg {
				margin-bottom: 18px; } } } }
.courses {
	margin-bottom: 95px;
	margin-top: 108px;
	h2 {
		margin-bottom: 9px;
		font-weight: bold;
		font-size: 30px;
		line-height: 54px; }
	p {
		font-weight: 500;
		font-size: 18px;
		line-height: 22px; }
	.flex {
		justify-content: space-between;
		gap: 24px;
		margin-top: 33px;
		.courses__item {
			padding-top: 25px;
			padding-left: 24px;
			padding-right: 24px;
			padding-bottom: 41px;
			box-shadow: 0px 0px 6px rgba(102, 113, 115, 0.2);
			h4 {
				font-weight: bold;
				font-size: 18px;
				line-height: 22px;
				margin-top: 34px; }
			p {
				margin-top: 18px;
				font-size: 14px;
				line-height: 22px; }
			.btn {
				letter-spacing: 0.06em;
				text-transform: uppercase;
				margin-top: 29px;
				max-width: 326px;
				height: 50px;
				border: none;
				color: white;
				font-weight: 800;
				font-size: 14px;
				line-height: 22px;
				&:hover {
					border: 2px solid #F3AF5A;
					background: inherit;
					color: #F3AF5A; }
				&:active {
					background: #E69837;
					color: white; }
				&:hover svg {
					fill: #F3AF5A; }
				svg {
					margin-left: 9px; } } } }
	a {
		text-align: center;
		margin: 0 auto;
		margin-top: 51px;
		color: #2C4267;
		font-weight: bold;
		font-size: 18px;
		line-height: 22px;
		border-bottom: 1px solid #2c4267;
		display: flex;
		justify-content: center;
		max-width: 135px; } }
.interes {
	margin-bottom: 100px;
	h2 {
		font-weight: bold;
		font-size: 30px;
		line-height: 54px;
		margin-bottom: 9px; }
	p {
		font-weight: 500;
		font-size: 18px;
		line-height: 22px;
		margin-bottom: 45px; }
	.flex {
		justify-content: space-between;
		.interes__item {
			max-width: 392px;
			width: 100%;
			box-shadow: 0px 0px 6px rgba(102, 113, 115, 0.2);
			padding-top: 25px;
			padding-right: 35px;
			padding-left: 35px;
			padding-bottom: 29px;
			.flex {
				justify-content: normal;
				margin-bottom: 19px;
				.title {
					margin-left: 12px;
					font-weight: bold;
					font-size: 18px;
					line-height: 22px; } }
			.crumbs {
				font-size: 11px;
				line-height: 13px;
				color: #4B4F58;
				font-family: Tahoma;
				margin-bottom: 19px;
				span {
					color: #94969B; } }
			.description {
				font-size: 14px;
				line-height: 22px; } } } }
.mail {
	padding-bottom: 69px;
	padding-top: 65px;
	background: linear-gradient(104.04deg, #35C9C5 14%, #00A9DB 84.88%);
	.mail__item {
		margin-right: 54px;
		h4 {
			font-weight: 800;
			color: white;
			font-size: 18px;
			line-height: 22px; }
		p {
			color: white;
			font-weight: 500;
			font-size: 16px;
			line-height: 22px; } }
	input {
		padding-left: 25px;
		border-radius: 4px;
		max-width: 408px;
		width: 100%;
		height: 50px;
		margin-right: 8px;
		font-weight: 500;
		font-size: 14px;
		line-height: 22px;
		color: #6D6D6D; }
	.btn {
		border-radius: 4px;
		max-width: 208px;
		width: 100%;
		height: 50px;
		font-weight: 800;
		font-size: 14px;
		line-height: 22px;
		letter-spacing: 0.06em;
		text-transform: uppercase;
		&:hover {
			border: 2px solid #F3AF5A;
			background: inherit;
			color: #F3AF5A; }
		&:active {
			background: #E69837;
			color: white; }
		&:hover svg {
			fill: #F3AF5A; } } }
.contact {
	margin-top: 90px;
	margin-bottom: 142px;
	.flex {
		gap: 101px;
		.dop {
			width: 100%;
			max-width: 703px; }
		.contact__item {
			h2 {
				font-weight: bold;
				font-size: 30px;
				line-height: 54px;
				margin-bottom: 12px; }
			.dop__text-one {
				font-weight: 500;
				font-size: 14px;
				line-height: 22px;
				margin-bottom: 42px;
				max-width: 420px;
				width: 100%; }
			.dop__text-two {
				font-weight: 500;
				font-size: 14px;
				margin-top: 32px;
				line-height: 22px;
				max-width: 420px;
				width: 100%; }
			.flex {
				gap: 0px;
				margin-bottom: 30px;
				svg {
					margin-right: 12px; }
				.text {
					font-weight: 600;
					font-size: 16px;
					line-height: 19px;
					color: black; } }
			h4 {
				font-weight: bold;
				font-size: 18px;
				line-height: 22px;
				margin-bottom: 24px; }
			form {
				max-width: 703px;
				width: 100%;
				input {
					border: 0.5px solid #777777;
					border-radius: 4px;
					font-weight: 500;
					font-size: 14px;
					line-height: 22px;
					color: #777777;
					height: 44px;
					max-width: 343px;
					width: 100%;
					padding-left: 17px;
					margin-bottom: 16px; }
				.dop {
					margin-right: 12px; }
				textarea {
					padding-top: 12px;
					padding-left: 17px;
					border: 0.5px solid #777777;
					border-radius: 4px;
					font-weight: 500;
					font-size: 14px;
					line-height: 22px;
					color: #777777;
					max-width: 703px;
					height: 120px;
					width: 100%; }
				.btn {
					letter-spacing: 0.06em;
					text-transform: uppercase;
					font-weight: 800;
					font-size: 14px;
					line-height: 22px;
					background: #2C4267;
					border-radius: 4px;
					max-width: 300px;
					height: 50px;
					margin-top: 19px; } } } } }
